/* Route Chips */
.route-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  min-height: 40px;
  align-items: center;
  padding: 4px;
  border: 2px solid var(--border);
  border-radius: var(--radius);
}

.route-chip {
  background: #e0e0e0;
  padding: 6px 12px;
  border-radius: var(--radius);
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.remove-chip-btn {
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  padding: 0;
  font-size: 16px;
  line-height: 1;
}

.remove-chip-btn:hover {
  color: #ff4444;
}

/* Country Code Styles */
.contact-input-group {
  display: flex;
  gap: 8px;
  position: relative;
}

.contact-input-group select {
  width: 15%;
  flex: 0 0 120px;
  padding: 0;
  margin-top: 8px;
  border: 2px solid var(--border);
  border-radius: var(--radius);
  appearance: none;
  background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e") 
    no-repeat right 1rem center/1em;
}

.contact-input-group option {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.contact-input-group option::before {
  content: attr(data-flag);
  margin-right: 0.5rem;
  font-size: 1.2em;
}

.loading-countries {
  padding: 0.75rem;
  color: var(--text-light);
  font-size: 0.875rem;
  text-align: center;
}

/* Enhanced Phone Number Input */
.contact-input-group input[type="text"] {
  flex: 1;
  padding: 0.75rem;
  border: 2px solid var(--border);
  border-radius: var(--radius);
}

/* Error State Adjustments */
.contact-input-group .error {
  border-color: var(--danger);
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

/* Flag Display in Dropdown */
.contact-input-group option[data-flag] {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
}

/* Loading Animation */
@keyframes loading-pulse {
  0% { opacity: 0.6; }
  50% { opacity: 1; }
  100% { opacity: 0.6; }
}

.loading-countries {
  animation: loading-pulse 1.5s ease-in-out infinite;
}

/* Country Code Flag Alignment */
.contact-input-group select {
  display: flex;
  align-items: center;
  padding-left: 2.5rem;
  background-position: left 0.75rem center;
}

.contact-input-group select option {
  padding-left: 2rem;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 1.5em;
}

/* Route Dropdown Styles */
.route-inputs select {
  margin-top: 8px;
  width: 100%;
  padding: 0 8px;
  border: 2px solid var(--border);
  border-radius: var(--radius);
  font-size: 1rem;
  appearance: none;
  background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e") 
    no-repeat right 0.75rem center/1em;
  background-color: white;
}

.route-inputs select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

.route-inputs select option {
  padding: 0.5rem;
}

/* Disabled Add Button Style */
.add-route-btn:disabled {
  background-color: #e2e8f0;
  color: #94a3b8;
  cursor: not-allowed;
}

/* Dropdown Container Positioning */
.route-inputs {
  position: relative;
}

.route-inputs::after {
  content: "";
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}

/* Submission notification styles */
.submission-notification {
  position: fixed;
  top: 1rem;
  left: 1rem;
  right: 1rem;
  background: white;
  border-radius: var(--radius);
  padding: 1rem;
  box-shadow: var(--shadow-md);
  z-index: 1000;
  animation: notificationSlideIn 0.3s ease-out forwards;
  border-left: 4px solid var(--success);
}

@keyframes notificationSlideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.close-notification {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: none;
  border: none;
  font-size: 1.25rem;
  cursor: pointer;
  color: var(--text-light);
}

.notification-content {
  margin-top: 0.5rem;
}

/* Hide horizontal scrollbar when not needed */
.table-container::-webkit-scrollbar {
  height: 6px;
}

.table-container::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}

.table-container::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.05);
}

/* Animation for Assign Popup */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.assign-popup {
  animation: slideIn 0.3s ease-out forwards;
}

/* Bus Table Assign Column */
.bus-table td:nth-child(6) {
  min-width: 150px;
}

/* RESPONSIVE STYLES */

/* For large screens (desktop) */
@media (min-width: 1201px) {
  .bus-table th, 
  .bus-table td {
    padding: 1rem 1.5rem;
  }
  
  .action-buttons {
    justify-content: center;
  }
}

/* For medium screens (tablets, small laptops) */
@media (max-width: 1200px) {
  .route-tags {
    max-width: 150px;
  }
  
  .route-tag {
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  .bus-table th, 
  .bus-table td {
    padding: 0.75rem;
  }
}

/* For smaller screens (tablets) */
@media (max-width: 992px) {
  .action-buttons {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  
  .action-buttons button {
    width: 2.25rem;
    height: 2.25rem;
  }
  
  .action-icon {
    width: 1rem;
    height: 1rem;
  }
}

/* For mobile devices */
@media (max-width: 768px) {
  .bus-page {
    padding: 1rem;
  }
  
  .control-bar {
    flex-direction: column;
    gap: 1rem;
  }
  
  .search-container {
    max-width: 100%;
    width: 100%;
  }
  
  .add-bus-btn {
    width: 100%;
    justify-content: center;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  .bus-table th, 
  .bus-table td {
    padding: 0.75rem;
  }
  
  .action-buttons {
    flex-direction: row;
    justify-content: center;
    gap: 0.5rem;
  }
  
  .action-buttons button {
    width: 2rem;
    height: 2rem;
  }
}

/* For smaller mobile devices */
@media (max-width: 640px) {
  .popup-content form label {
    margin-bottom: 1.5rem;
  }
  
  .contact-input-group {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .contact-input-group select {
    width: 100%;
    flex: none;
  }
  
  .contact-input-group input {
    width: 100%;
  }
  
  .popup-actions {
    flex-direction: column;
  }
  
  .popup-actions button {
    width: 100%;
  }
  
  .route-chip {
    font-size: 12px;
    padding: 4px 8px;
  }
  
  .remove-chip-btn {
    font-size: 14px;
  }
  
  .assign-popup {
    width: 95%;
    padding: 1rem;
  }
  
  .driver-item {
    padding: 0.75rem;
  }
}

/* For very small mobile devices */
@media (max-width: 480px) {
  .popup-content {
    width: 95%;
    max-width: 100%;
    padding: 1.5rem;
  }
  
  h1 {
    font-size: 1.75rem;
  }
  
  .route-inputs {
    flex-direction: column;
  }
  
  .route-inputs input, 
  .route-inputs select {
    width: 100%;
  }
  
  .route-inputs-group {
    flex-direction: column;
  }
  
  .add-route-btn {
    width: 100%;
  }
  
  .driver-item {
    padding: 0.75rem 0.5rem;
  }
  
  .driver-list {
    max-height: 250px;
  }
  
  .submission-notification {
    font-size: 0.9rem;
  }
  
  .notification-content p {
    margin: 0.25rem 0;
  }
}

/* For smaller height screens */
@media (max-height: 700px) {
  .popup-content {
    max-height: 90vh;
    overflow-y: auto;
    padding: 1rem;
  }
  
  .driver-list {
    max-height: 200px;
  }
}

/* Touch-friendly improvements for mobile devices */
@media (hover: none) and (pointer: coarse) {
  .action-buttons button {
    min-height: 44px;
    min-width: 44px;
  }
  
  input, select, button {
    min-height: 44px;
  }
  
  .remove-chip-btn {
    min-height: 30px;
    min-width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .popup-content form input,
  .popup-content form select {
    font-size: 16px; /* Prevents iOS zoom on focus */
  }
}

/* Fix for Safari issues with flex containers */
@supports (-webkit-touch-callout: none) {
  .route-inputs {
    width: 100%;
  }
  
  .route-inputs-group {
    width: 100%;
  }
}


/* Add to existing button styles */
.button-group {
  display: flex;
  gap: 10px;
}

.import-csv-btn {
  background-color: #4CAF50;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background-color 0.3s;
}

.import-csv-btn:hover {
  background-color: #45a049;
}

.csv-icon {
  width: 18px;
  height: 18px;
  fill: currentColor;
}


/* Parent Login Popup Styles */
.assign-popup {
  width: 400px;
  max-width: 90%;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}

.login-form label {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  font-weight: 500;
  color: #334155;
}

.login-form input {
  padding: 0.75rem;
  border: 1px solid #cbd5e1;
  border-radius: 0.375rem;
  font-size: 1rem;
}

.login-form input:focus {
  outline: none;
  border-color: #60a5fa;
  box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.2);
}

/* Parent Login Display in Table */
.parent-login {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.parent-login div:first-child {
  font-weight: 500;
  color: #1e293b;
}

.parent-login div:last-child {
  color: #64748b;
  margin-top: 0.25rem;
}

/* Login Button Styles */
.login-btn {
  background-color: #f0fdf4;
  color: #16a34a;
  border: 1px solid #bbf7d0;
}

.login-btn:hover {
  background-color: #dcfce7;
}

.login-btn .action-icon {
  fill: #16a34a;
}

/* Status Indicators */
.unassigned {
  color: #64748b;
  font-style: italic;
  font-size: 0.875rem;
}

/* Popup Action Buttons Specific to Login */
.assign-confirm-btn {
  background-color: #10b981;
  color: white;
}

.assign-confirm-btn:hover {
  background-color: #059669;
}

.assign-cancel-btn {
  background-color: #f1f5f9;
  color: #64748b;
}

.assign-cancel-btn:hover {
  background-color: #e2e8f0;
}

/* Responsive Adjustments */
@media (max-width: 640px) {
  .assign-popup {
    width: 100%;
    padding: 1rem;
  }
  
  .parent-login div {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 120px;
  }
}