/* Dashboard.css */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css');

:root {
  --driver-color: #4361ee;
  --student-color: #06d6a0;
  --payment-color: #7209b7;
  --bus-color: #f3722c;
  --success: #06d6a0;
  --warning: #ffd60a;
  --error: #ef476f;
  --background: #f8f9fa;
  --text-primary: #2b2d42;
  --text-secondary: #8d99ae;
}

.dashboard {
  padding: 2rem;
  font-family: 'Inter', sans-serif;
  background: var(--background);
  min-height: 100vh;
}

.dashboard-title {
  text-align: center;
  color: var(--text-primary);
  font-size: 2.5rem;
  margin-bottom: 2rem;
  font-weight: 700;
  letter-spacing: -0.5px;
  position: relative;
  display: inline-block;
  width: 100%;
}

.dashboard-title::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: var(--driver-color);
  margin: 0.5rem auto;
  border-radius: 2px;
}

.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.dashboard-card {
  background: white;
  border-radius: 20px;
  padding: 1.5rem;
  box-shadow: 0 8px 30px rgba(0,0,0,0.05);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(0,0,0,0.05);
  display: flex;
  flex-direction: column;
  min-height: 300px;
}

.dashboard-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0,0,0,0.1);
}

.card-shine {
  position: absolute;
  top: 0;
  left: -100%;
  width: 50%;
  height: 100%;
  background: linear-gradient(
    90deg,
    rgba(255,255,255,0) 0%,
    rgba(255,255,255,0.3) 50%,
    rgba(255,255,255,0) 100%
  );
  transition: 0.6s;
}

.dashboard-card:hover .card-shine {
  left: 150%;
}

.card-header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  position: relative;
}

.icon-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  background: linear-gradient(135deg, var(--driver-color) 0%, #3a0ca3 100%);
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.student-card .icon-wrapper { background: linear-gradient(135deg, var(--student-color) 0%, #0496ff 100%); }
.payment-card .icon-wrapper { background: linear-gradient(135deg, var(--payment-color) 0%, #480ca8 100%); }
.bus-card .icon-wrapper { background: linear-gradient(135deg, var(--bus-color) 0%, #f8961e 100%); }

.card-icon {
  font-size: 1.4rem;
  color: white;
}

.card-header h2 {
  margin: 0;
  color: var(--text-primary);
  font-size: 1.4rem;
  font-weight: 600;
}

.card-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex: 1;
}

.metric-group {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  flex: 1;
}

.metric {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.metric.full-width {
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
}

.metric-label {
  color: var(--text-secondary);
  font-size: 0.95rem;
  font-weight: 500;
}

.metric-value-container {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.metric-value {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text-primary);
}

.active { color: var(--success); }
.inactive { color: var(--error); }
.success { color: var(--success); }
.warning { color: var(--warning); }

.metric-trend {
  font-size: 0.8rem;
  font-weight: 600;
  padding: 0.2rem 0.5rem;
  border-radius: 20px;
}

.metric-trend.up {
  background: rgba(6, 214, 160, 0.15);
  color: var(--success);
}

.metric-trend.down {
  background: rgba(239, 71, 111, 0.15);
  color: var(--error);
}

.status-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.status-dot.active { background: var(--success); }
.status-dot.inactive { background: var(--error); }
.status-dot.warning { background: var(--warning); }

.route-badges {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.route-badge {
  background: rgba(67, 97, 238, 0.1);
  color: var(--driver-color);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

.see-more-btn {
  margin: 20px 0 0 0; /* Added 8px margin to all sides */
  padding: 0.8rem 1.5rem;
  background: linear-gradient(135deg, var(--driver-color) 0%, #3a0ca3 100%);
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
}

.student-card .see-more-btn { background: linear-gradient(135deg, var(--student-color) 0%, #0496ff 100%); }
.payment-card .see-more-btn { background: linear-gradient(135deg, var(--payment-color) 0%, #480ca8 100%); }
.bus-card .see-more-btn { background: linear-gradient(135deg, var(--bus-color) 0%, #f8961e 100%); }

.see-more-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.15);
}

.see-more-btn i {
  font-size: 0.9rem;
  transition: transform 0.3s ease;
}

.see-more-btn:hover i {
  transform: translateX(3px);
}

.metric-divider {
  height: 1px;
  background: rgba(0,0,0,0.05);
  margin: 0.5rem 0;
}

@media (max-width: 768px) {
  .dashboard {
    padding: 1.5rem;
  }
  
  .dashboard-title {
    font-size: 2rem;
  }
  
  .dashboard-grid {
    grid-template-columns: 1fr;
  }
}

@keyframes cardEntrance {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.dashboard-card {
  animation: cardEntrance 0.6s ease forwards;
  opacity: 0;
}

.dashboard-card:nth-child(1) { animation-delay: 0.1s; }
.dashboard-card:nth-child(2) { animation-delay: 0.2s; }
.dashboard-card:nth-child(3) { animation-delay: 0.3s; }
.dashboard-card:nth-child(4) { animation-delay: 0.4s; }