/* MainLayout.css - Modern & Attractive Design */
:root {
  --sidebar-width: 280px;
  --navbar-height: 70px;
  --primary-color: #0f172a;      /* Dark blue background */
  --secondary-color: #f1f5f9;    /* Light gray background */
  --accent-color: #38bdf8;       /* Bright blue accent */
  --accent-hover: #0ea5e9;       /* Darker blue on hover */
  --text-color: #f8fafc;         /* Light text for dark backgrounds */
  --text-dark: #1e293b;          /* Dark text for light backgrounds */
  --text-muted: #94a3b8;         /* Muted text color */
  --hover-color: #1e293b;        /* Slightly lighter than primary for hover */
  --border-color: #334155;       /* Border color for dark theme */
  --card-bg: #ffffff;            /* Card background */
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.2);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.15);
  --shadow-lg: 0 8px 30px rgba(0, 0, 0, 0.2);
  --transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  --danger-color: #f43f5e;       /* Red for danger actions */
  --danger-hover: #e11d48;       /* Darker red on hover */
  --success-color: #10b981;      /* Green for success indicators */
  --border-radius: 12px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Inter', 'SF Pro Display', -apple-system, BlinkMacSystemFont, sans-serif;
}

body {
  background-color: var(--secondary-color);
  color: var(--text-dark);
}

.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Navbar Styles */
.navbar {
  display: flex;
  align-items: center;
  height: var(--navbar-height);
  background-color: var(--primary-color);
  color: var(--text-color);
  padding: 0 30px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  box-shadow: var(--shadow-sm);
}

.navbar.mobile {
  justify-content: space-between;
  padding: 0 20px;
}

.toggle-btn {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: var(--text-color);
  cursor: pointer;
  display: none;
  transition: var(--transition);
  padding: 10px;
  border-radius: 10px;
}

.toggle-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
  transform: scale(1.05);
}

.navbar-brand {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--accent-color);
  display: flex;
  align-items: center;
  gap: 10px;
  letter-spacing: -0.5px;
}

.navbar-items {
  display: flex;
  gap: 16px;
  margin-left: auto;
  align-items: center;
}

.nav-item {
  color: var(--text-color);
  text-decoration: none;
  padding: 8px 16px;
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  gap: 10px;
  transition: var(--transition);
  font-size: 0.95rem;
  font-weight: 500;
  background-color: rgba(255, 255, 255, 0.05);
}

.nav-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--accent-color);
  transform: translateY(-2px);
}

.nav-icon {
  font-size: 1.2rem;
}

.logout-btn {
  background-color: rgba(244, 63, 94, 0.2);
  color: var(--danger-color);
  border: none;
  cursor: pointer;
  font-weight: 500;
}

.logout-btn:hover {
  background-color: rgba(244, 63, 94, 0.3);
  color: var(--danger-hover);
}

/* Sidebar Styles */
.sidebar {
  position: fixed;
  left: 0;
  top: var(--navbar-height);
  height: calc(100vh - var(--navbar-height));
  width: var(--sidebar-width);
  background-color: var(--primary-color);
  color: var(--text-color);
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  z-index: 999;
  box-shadow: var(--shadow-sm);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.sidebar::-webkit-scrollbar {
  width: 6px;
}

.sidebar::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}

.sidebar::-webkit-scrollbar-track {
  background-color: transparent;
}

.sidebar.mobile {
  transform: translateX(-100%);
  box-shadow: var(--shadow-lg);
}

.sidebar.mobile.open {
  transform: translateX(0);
}

.sidebar-content {
  padding: 25px 0;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.sidebar-menu {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex: 1;
  padding: 0 20px;
}

.menu-item {
  color: var(--text-muted);
  text-decoration: none;
  padding: 14px 20px;
  border-radius: var(--border-radius);
  transition: var(--transition);
  background: transparent;
  display: flex;
  align-items: center;
  gap: 14px;
  font-size: 0.95rem;
  font-weight: 500;
  position: relative;
  overflow: hidden;
}

.menu-item:hover {
  background-color: var(--hover-color);
  color: var(--accent-color);
  transform: translateX(5px);
}

.menu-item.active {
  background-color: rgba(56, 189, 248, 0.15);
  color: var(--accent-color);
  font-weight: 600;
}

.menu-item.active::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background-color: var(--accent-color);
  border-radius: 0 4px 4px 0;
}

.menu-item.active .menu-icon {
  color: var(--accent-color);
}

.menu-icon {
  font-size: 1.3rem;
  color: var(--text-muted);
  transition: var(--transition);
}

.menu-item:hover .menu-icon {
  color: var(--accent-color);
}

.menu-item span {
  flex: 1;
}

.logout-item {
  margin-top: auto;
  background-color: rgba(244, 63, 94, 0.15);
  color: var(--danger-color);
  cursor: pointer;
}

.logout-item:hover {
  background-color: rgba(244, 63, 94, 0.25);
  color: var(--danger-hover);
}

.logout-item .menu-icon {
  color: var(--danger-color);
}

/* Content Overlay */
.content-overlay {
  position: fixed;
  top: var(--navbar-height);
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 998;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  transition: var(--transition);
}

/* Content Area */
.content {
  margin-top: var(--navbar-height);
  margin-left: 0;
  flex: 1;
  transition: var(--transition);
  padding: 30px;
}

.content.with-sidebar {
  margin-left: var(--sidebar-width);
}

.content-inner {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-md);
  padding: 30px;
  min-height: calc(100vh - var(--navbar-height) - 60px);
  transition: var(--transition);
  border: none;
}

.content-inner:hover {
  box-shadow: var(--shadow-lg);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .toggle-btn {
    display: block;
  }

  .navbar-items {
    display: none;
  }

  .sidebar {
    width: 300px;
  }

  .content {
    margin-left: 0;
    padding: 20px;
  }

  .content-inner {
    padding: 25px;
  }
}

@media (max-width: 480px) {
  .sidebar {
    width: 85%;
  }
  
  .content {
    padding: 15px;
  }
  
  .content-inner {
    padding: 20px;
    border-radius: 10px;
  }

  .navbar {
    padding: 0 15px;
    height: 65px;
  }
  
  .navbar-brand {
    font-size: 1.2rem;
  }
  
  :root {
    --navbar-height: 65px;
  }
}

/* Additional Card Styles for Content Area */
.card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-sm);
  padding: 20px;
  margin-bottom: 20px;
  transition: var(--transition);
}

.card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--secondary-color);
}

.card-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-dark);
}

.card-content {
  color: var(--text-dark);
}

/* Button Styles */
.btn {
  padding: 10px 16px;
  border-radius: var(--border-radius);
  font-weight: 500;
  font-size: 0.95rem;
  cursor: pointer;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  gap: 8px;
  border: none;
}

.btn-primary {
  background-color: var(--accent-color);
  color: white;
}

.btn-primary:hover {
  background-color: var(--accent-hover);
  transform: translateY(-2px);
}

.btn-danger {
  background-color: var(--danger-color);
  color: white;
}

.btn-danger:hover {
  background-color: var(--danger-hover);
  transform: translateY(-2px);
}

/* Badges */
.badge {
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
}

.badge-success {
  background-color: rgba(16, 185, 129, 0.15);
  color: var(--success-color);
}

.badge-danger {
  background-color: rgba(244, 63, 94, 0.15);
  color: var(--danger-color);
}