/* Modern School Profile CSS with a1-a100 class names */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

/* Main container */
.a1 {
  font-family: 'Poppins', sans-serif;
  max-width: 2000px;
  margin: 0 auto;
  background-color: #ffffff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

/* Banner Section */
.a2 {
  height: 250px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.a3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.5) 50%, rgba(0, 0, 0, 0.2) 100%);
  display: flex;
  align-items: center;
}

.a4 {
  display: flex;
  align-items: center;
  padding: 0 40px;
  color: #ffffff;
}

.a5 {
  margin-right: 25px;
  position: relative;
  border-radius: 50%;
  padding: 3px;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

.a6 {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #ffffff;
}

.a7 {
  display: flex;
  flex-direction: column;
}

.a8 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.a9 {
  display: flex;
  align-items: center;
  font-size: 1rem;
  margin-top: 8px;
}

.a10 {
  font-weight: 500;
}

.a11 {
  display: inline-block;
  width: 4px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 50%;
  margin: 0 12px;
}

.a12 {
  font-style: italic;
}

/* Content Wrapper */
.a13 {
  padding: 30px;
}

/* Quick Stats */
.a14 {
  display: flex;
  justify-content: space-between;
  margin: -70px 0 30px;
  position: relative;
}

.a15 {
  flex: 1;
  margin: 0 10px 0 0;
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  align-items: center;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #76daff, #2396f3);
}

.a15:hover {
  transform: translateY(-5px);
}

.a16 {
  font-size: 2rem;
  margin-right: 15px;
  opacity: 0.9;
}

.a17 {
  display: flex;
  flex-direction: column;
}

.a18 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
}

.a19 {
  font-size: 0.9rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.9);
}

.a20 {
  flex: 1;
  margin: 0 10px;
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  align-items: center;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #fdd835, #ff9800);
}

.a20:hover {
  transform: translateY(-5px);
}

.a21 {
  font-size: 2rem;
  margin-right: 15px;
  opacity: 0.9;
}

.a22 {
  display: flex;
  flex-direction: column;
}

.a23 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
}

.a24 {
  font-size: 0.9rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.9);
}

.a25 {
  flex: 1;
  margin: 0 0 0 10px;
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  align-items: center;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #66bb6a, #43a047);
}

.a25:hover {
  transform: translateY(-5px);
}

.a26 {
  font-size: 2rem;
  margin-right: 15px;
  opacity: 0.9;
}

.a27 {
  display: flex;
  flex-direction: column;
}

.a28 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #ffffff;
}

.a29 {
  font-size: 0.9rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.9);
}

/* Profile Sections */
.a30 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
}

.a31 {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 25px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.a32 {
  color: #2d3748;
  font-size: 1.5rem;
  margin-top: 0;
  margin-bottom: 20px;
  padding-bottom: 10px;
  position: relative;
}

.a32:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background: #3a7bd5;
}

/* Contact Info */
.a33 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.a34, .a39, .a44, .a49, .a54 {
  display: flex;
  align-items: flex-start;
}

.a35, .a40, .a45, .a50, .a55 {
  font-size: 1.5rem;
  margin-right: 15px;
  color: #3a7bd5;
}

.a36, .a41, .a46, .a51, .a56 {
  display: flex;
  flex-direction: column;
}

.a37, .a42, .a47, .a52, .a57 {
  font-size: 0.85rem;
  font-weight: 600;
  color: #4a5568;
  margin-bottom: 3px;
}

.a38, .a43, .a48, .a53, .a58 {
  font-size: 0.95rem;
  color: #2d3748;
}

/* Bus Tracker */
.a59 {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 25px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.a60 {
  color: #2d3748;
  font-size: 1.5rem;
  margin-top: 0;
  margin-bottom: 20px;
  padding-bottom: 10px;
  position: relative;
}

.a60:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background: #3a7bd5;
}

.a61 {
  display: flex;
  flex-direction: column;
}

.a62 p {
  color: #4a5568;
  margin-top: 0;
  line-height: 1.6;
}

.a63 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  list-style-type: none;
  padding: 0;
  margin: 15px 0;
}

.a63 li {
  position: relative;
  padding-left: 25px;
  color: #4a5568;
}

.a63 li:before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #ff9800;
  font-weight: bold;
}

.a64 {
  background: linear-gradient(135deg, #3a7bd5, #2a5a9e);
  color: #ffffff;
  border: none;
  border-radius: 8px;
  padding: 14px 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(58, 123, 213, 0.3);
  margin-top: 15px;
  align-self: flex-start;
}

.a64:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(58, 123, 213, 0.4);
}

.a64:active {
  transform: translateY(0);
}

/* Responsive Design */
@media (max-width: 900px) {
  .a30 {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .a4 {
    flex-direction: column;
    text-align: center;
    padding: 20px;
  }

  .a5 {
    margin-right: 0;
    margin-bottom: 15px;
  }

  .a14 {
    flex-direction: column;
    margin-top: -40px;
  }

  .a15, .a20, .a25 {
    margin: 10px 0;
  }

  .a33 {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .a8 {
    font-size: 2rem;
  }

  .a2 {
    height: 200px;
  }

  .a6 {
    width: 90px;
    height: 90px;
  }

  .a31, .a59 {
    padding: 20px 15px;
  }

  .a63 {
    grid-template-columns: 1fr;
  }
}