/* Filter group styling */
.filter-group {
  display: flex;
  gap: 10px;
}

/* Type filter styling */
.type-filter {
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ddd;
  background-color: white;
  font-size: 14px;
  color: #333;
  cursor: pointer;
  transition: all 0.3s ease;
}

.type-filter:hover {
  border-color: #aaa;
}

.type-filter:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

/* Make status and type filters consistent */
.status-filter, .type-filter {
  min-width: 120px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .filter-group {
    flex-direction: column;
    gap: 8px;
  }
  
  .status-filter, .type-filter {
    width: 100%;
  }
}